    import { mapState } from "vuex";
    import uMask from "../../../components/basic-component/u-mask/u-mask.vue";

    export default {
        name: "cash",
	    data() {
            return {
                tabList: [
                    {id:1, name: '出售中'},
                    {id:0, name: '下架中'}
                ],
                activeTab: '1',
                detailDialog: false,
                searchBool: false,
	            list: [],
                tab: [],
                detail: {},
                titleShow: false,
	            type: 'share',
                titleWidth: null,
                getFocus: false,
                status: 0,
	            over: false,
                model: false,
                modelType: -1,
                textarea: '',
	            setItem: {},
                page: 1,
                inputText: '',
            }
	    },
        components: {
            "u-mask": uMask
        },
	    onLoad(options) { this.$commonLoad.onload(options);
            this.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            this.getTabs();
	    },
        onReachBottom() {
            if (!this.over) {
                this.page++;
                this.bottomRequest();
            }
        },
	    methods: {
            toDetail(detail) {
                this.detail = detail;
                this.detailDialog = true;
            },
            getTabs() {
                let that = this;
                that.$request({
                    url: that.$api.app_admin.cash_tabs,
                    method: 'get'
                }).then(response=>{
                    that.$hideLoading();
                    uni.hideLoading();
                    if(response.code == 0) {
                        that.tab = response.data;
                        if(that.tab.length == 1) {
                            uni.setNavigationBarTitle({
                                title: that.tab[0].plugin + '提现'
                            })
                        }else {
                            that.titleShow = true;
                        }
                        that.titleWidth = 1/that.tab.length * 100 + '%';
                        that.type = response.data[0].key;
                        that.$request({
                            url: that.$api.app_admin.cash,
                            data: {
                                page: 1,
                                model_type: that.type,
                                status: 0,
                                keyword: '',
                            }
                        }).then(response => {
                            if (response.code == 0) {
                                that.list = response.data.list;
                            }else {
                                uni.showToast({
                                    title: response.msg,
                                    icon: 'none',
                                    duration: 1000
                                });
                            }
                        })
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                })
            },
            searchText() {
                let that = this;
                setTimeout(v=>{
                    that.page = 1;
                    that.over = false;
                    that.getFocus=false;
                    if(that.inputText == '') {
                        that.searchBool = false;
                    }
                    uni.showLoading({
                        mask: true,
                        title: '搜索中...'
                    });
                    that.switchRequest({type: 1});
                },300)
            },
            clearSearch() {
                this.inputText = '';
                if(!this.searchBool) {
                    this.searchText();
                }
            },
            bottomRequest() {
                this.$request({
                url: this.$api.app_admin.cash,
                    data: {
                        page: this.page,
                        model_type: this.type,
                        status: this.status,
                        keyword: '',
                    }
                }).then(response => {
                    if (response.code === 0) {
                        if (response.data.list.length === 0) {
                            this.over = true;
                        } else {
                            this.list = [...this.list, ...response.data.list];
                        }
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                })
            },
            setStatus(data) {
                this.page = 1;
                this.status = data;
                this.over = false;
                this.inputText = '';
                this.searchBool = false;
                uni.showLoading({
                    mask: true,
                    title: '加载中...'
                });
                this.$request({
	                url: this.$api.app_admin.cash,
	                data: {
	                    page: 1,
		                model_type: this.type,
		                status: data,
		                keyword: this.inputText,
	                }
                }).then(response => {
                    uni.hideLoading();
                    if (response.code === 0) {
                        this.list = response.data.list;
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                });
            },
            tabSwitch(data) {
                this.page = 1;
                this.status = 0;
                this.over = false;
                this.type = data;
                this.inputText = '';
                this.searchBool = false;
                this.list = [];
                uni.showLoading({
                    mask: true,
                    title: '加载中...'
                });
                this.$request({
	                url: this.$api.app_admin.cash,
	                data: {
                        page: 1,
		                model_type: data,
                        status: 0,
		                keyword: this.inputText,
	                }
                }).then(response => {
                    uni.hideLoading();
                    if (response.code === 0) {
                        this.list = response.data.list;
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                });
            },
            confirm() {
                // 拒绝
	            if (this.modelType === 1) {
	                if (this.textarea === '') {
                        uni.showToast({
                            title: '请填写拒绝理由',
                            icon: 'none',
                            duration: 1000
                        });
                        return false
                    };
                    this.$request({
                        url: this.$api.app_admin.cash_save,
                        method: 'post',
                        data: {
                            status: 3,
                            id: this.setItem.id,
                            content: this.textarea,
                            model_type: this.type,
                        }
                    }).then(response => {
                        if (response.code === 0) {
                            for (let i = 0; i < this.list.length; i++) {
                                if (this.list[i].id === this.setItem.id) {
                                    this.$delete(this.list, i);
                                    this.model = false;
                                    this.textarea = '';
                                }
                            }
                        }else {
                            uni.showToast({
                                title: response.msg,
                                icon: 'none',
                                duration: 1000
                            });
                        }
                    });
	            } else if (this.modelType === 3) {
                    this.$request({
                        url: this.$api.app_admin.cash_save,
                        method: 'post',
                        data: {
                            id: this.setItem.id,
                            content: '小程序端操作',
                            model_type: this.type,
                            status: 2,
                        }
                    }).then(response => {
                        if (response.code === 0) {
                            for (let i = 0; i < this.list.length; i++) {
                                if (this.list[i].id === this.setItem.id) {
                                    this.$delete(this.list, i);
                                    this.model = false;
                                }
                            }
                        }else {
                            uni.showToast({
                                title: response.msg,
                                icon: 'none',
                                duration: 1000
                            });
                        }
                    })
	            } else if (this.modelType === 2) {
                    this.$request({
                        url: this.$api.app_admin.cash_save,
                        method: 'post',
                        data: {
                            id: this.setItem.id,
                            content: '小程序端操作',
                            model_type: this.type,
                            status: 1,
                        }
                    }).then(response => {
                        if (response.code === 0) {
                            for (let i = 0; i < this.list.length; i++) {
                                if (this.list[i].id === this.setItem.id) {
                                    this.$delete(this.list, i);
                                    this.model = false;
                                    this.textarea = '';
                                }
                            }
                        }else {
                            uni.showToast({
                                title: response.msg,
                                icon: 'none',
                                duration: 1000
                            });
                        }
                    });
	            }
            },
            refuse(data) {
                this.setItem = data;
                this.model = true;
                this.modelType = 1;
            },
            byGo(data) {
                this.setItem = data;
                this.model = true;
                this.modelType = 2;
            },
            pay(data) {
                this.setItem = data;
                this.model = true;
                this.modelType = 3;
            },
		    async request(data) {
                const response = await this.$request({
                    url: this.$api.app_admin.cash,
                    data: {
                        model_type: this.type,
                        page: data.page,
                        status: data.status,
                        keyword: this.inputText,
                    }
                });
                uni.hideLoading();
                if (response.code === 0) {
                    this.list = response.data.list;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
		    },
		    switchRequest(data) {
                if (this.status === 0) {
                    this.request({
                        page: this.page,
                        status: 0,
                    });
                } else if (this.status === 1) {
                    this.request({
                        page: this.page,
                        status: 1,
                    });
                }
		    }
	    }
    }
